import { SidenavComponent } from './../../components/sidenav/sidenav.component';
import { environment } from './../../../environments/environment';
import { Component, HostListener, OnInit, ViewChild } from '@angular/core';
import { MatrixDataService } from './../../services/matrix-data.service';
import { Color } from '@angular-material-components/color-picker';
import * as JSZip from 'jszip';
import * as FileSaver from 'file-saver';
import { LoadingData } from 'src/app/components/loader/loader.component';
import { SettingsSidenavComponent } from 'src/app/components/settings-sidenav/settings-sidenav.component';

export interface ButtonData {
  name: string;
  position: number[];
  color: Color;
  action: Action;
  musicfiles?: Blob[];
}

export interface Action {
  name: string;
  value: string;
  type?: string;
}

@Component({
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
})
export class MainComponent implements OnInit {
  gridSizeX = environment.gridSize[0];
  gridSizeY = environment.gridSize[1];

  rows: number[] = [];
  columns: number[] = [];

  actionToggleStatus: boolean = false;
  displayTypes = 2;
  displayedActionType = 0; //0=singel Tab Button Config, 1=double Tab Button Config

  matrixData: ButtonData[][][] = [[[]]];

  defaultBrightness: number = environment.defaultBrightness;

  defaultColor: Color = environment.defaultButtonColor;

  defaultAnimation: string = 'sbc';

  loadingData: LoadingData = {
    enabled: false,
    text: '',
    progress: 0,
  };

  downloadUrl = null; //needs to be global to be clearable

  @ViewChild('buttonconf') buttonConf: SidenavComponent;

  @ViewChild('settingsconf') settingsConf: SettingsSidenavComponent;

  @HostListener('window:beforeunload', ['$event'])
  onUnload($event) {
    this.saveMatrixData();
  }

  constructor(private matrixDataService: MatrixDataService) { }

  ngOnInit(): void {
    let savedColor: string = localStorage.getItem('matrixDefaultColor');
    if (savedColor != null) {
      let rgbValues = savedColor.split(',');
      this.defaultColor = new Color(
        Number(rgbValues[0]),
        Number(rgbValues[1]),
        Number(rgbValues[2])
      );
    }

    let savedData: string = localStorage.getItem('matrixConfiguration');
    if (savedData == null || !savedData.includes('^')) {
      this.setDefaultButtonArray();
      this.loadDefaultConfig(false);
    } else {
      if (savedData.length > 15) {
        try {
          this.matrixData = this.stringToButtonArray(savedData);
          this.gridSizeY = this.matrixData[0][0].length;
          this.gridSizeX = this.matrixData[0].length;
          for (let i = 0; i < this.gridSizeY; i++) {
            this.rows.push(i);
          }
          for (let i = 0; i < this.gridSizeX; i++) {
            this.columns.push(i);
          }
        } catch (err) {
          console.error(err);
          this.setDefaultButtonArray();
          this.loadDefaultConfig(false);
        }
      } else {
        this.setDefaultButtonArray();
        this.loadDefaultConfig(false);
      }
    }

    this.matrixDataService.changeData(this.matrixData);

    this.matrixDataService.currentData.subscribe((res) => {
      this.matrixData = res;
      console.log(res);
    });
  }

  buttonPressed(x: number, y: number) {
    console.log(this.matrixData[this.displayedActionType][x][y]);
    this.buttonConf.openButtonConfig(x, y);
  }

  setMatrixSize(size: number[]) {
    this.updateRowsColumns(size);
  }

  setBrightness(value: number) {
    this.defaultBrightness = value;
  }

  setDefAni(value: string) {
    this.defaultAnimation = value;
  }

  loadDefaultConfig(changeData: boolean) {
    this.matrixData = this.stringToButtonArray(environment.defaultConfig);
    this.gridSizeX = this.matrixData[0].length;
    this.gridSizeY = this.matrixData[0][0].length;
    if (changeData) {
      this.matrixDataService.changeData(this.matrixData);
    }
  }

  downloadSiteConfig() {
    let stringifiedConfig = this.stringifyMatrix(this.matrixData);

    FileSaver.saveAs(
      this.createBlobFromText(stringifiedConfig),
      'launchpad.config'
    );
  }

  loadSiteConfig(config) {
    this.matrixData = this.stringToButtonArray(config);
    this.matrixDataService.changeData(this.matrixData);
  }

  exportbmap() {
    let bMapText = this.generateBMap(this.matrixData);
    FileSaver.saveAs(this.createBlobFromText(bMapText), 'bmap.txt');
  }

  actionToImagePath(act: string): string {
    return 'assets/icons/' + act + '.svg';
  }

  saveMatrixData() {
    let stringConfig = this.stringifyMatrix(this.matrixData);
    localStorage.setItem('matrixConfiguration', stringConfig);

    localStorage.setItem(
      'matrixDefaultColor',
      [this.defaultColor.r, this.defaultColor.g, this.defaultColor.b].toString()
    );
  }

  stringifyMatrix(mdata: ButtonData[][][]): string {
    let stringifiedData: string[][][] = [[]];

    for (let h = 0; h < this.displayTypes; h++) {
      stringifiedData[h] = [];
      for (let i = 0; i < this.gridSizeX; i++) {
        stringifiedData[h][i] = [];
        for (let j = 0; j < this.gridSizeY; j++) {
          let stripedData: ButtonData = this.deepCopy(mdata[h][j][i]);
          stripedData.musicfiles = [];
          stringifiedData[h][i][j] = JSON.stringify(stripedData);
        }
      }
    }

    let stringData: string = this.threeDArrayToString(stringifiedData);
    return stringData;
  }

  threeDArrayToString(array: string[][][]): string {
    let returnString: string = '';
    for (let h = 0; h < this.displayTypes; h++) {
      for (let i = 0; i < this.gridSizeX; i++) {
        returnString += array[h][i].join(';');
        returnString += '|';
      }
      returnString += '^';
    }

    return returnString;
  }

  stringToButtonArray(savedData): ButtonData[][][] {
    let buttonArrayData: ButtonData[][][] = [[[]]];
    let stringArrayData: string[][][] = [[[]]];
    let displays: string[] = savedData.split('^');
    if (displays.length > 1) {
      displays.splice(-1, 1);
    }

    for (let h = 0; h < displays.length; h++) {
      let rowData: string[] = displays[h].split('|');
      rowData.splice(-1, 1);
      stringArrayData[h] = [];

      for (let i = 0; i < rowData.length; i++) {
        stringArrayData[h][i] = rowData[i].split(';');
      }
    }

    console.log(stringArrayData);

    for (let h = 0; h < displays.length; h++) {
      buttonArrayData[h] = [];
      for (let i = 0; i < this.gridSizeX; i++) {
        buttonArrayData[h][i] = [];
        for (let j = 0; j < this.gridSizeY; j++) {
          console.log(stringArrayData[h][i][j]);
          let data: ButtonData = JSON.parse(stringArrayData[h][j][i]);
          data.color = new Color(
            data.color.r,
            data.color.g,
            data.color.b,
            data.color.a
          );
          buttonArrayData[h][i][j] = data;
        }
      }
    }

    if (displays.length < this.displayTypes) {
      let diff = this.displayTypes - displays.length;
      for (let i = displays.length; i < this.displayTypes; i++) {
        buttonArrayData[i] = this.clearedButtonArray();
      }
    }

    console.log(buttonArrayData);

    return buttonArrayData;
  }

  setDefaultButtonArray() {
    this.rows = [];
    this.columns = [];
    for (let i = 0; i < this.gridSizeY; i++) {
      this.rows.push(i);
    }
    for (let i = 0; i < this.gridSizeX; i++) {
      this.columns.push(i);
    }

    for (let h = 0; h < this.displayTypes; h++) {
      this.matrixData[h] = this.clearedButtonArray();
    }
    this.matrixDataService.changeData(this.matrixData);
    console.log(this.matrixData);
  }

  clearedButtonArray(): ButtonData[][] {
    let buttonData: ButtonData[][] = [[]];

    for (let i = 0; i < this.gridSizeX; i++) {
      buttonData[i] = [];
      for (let j = 0; j < this.gridSizeY; j++) {
        buttonData[i][j] = {
          name: 'Button' + (i + j * this.gridSizeY),
          action: { name: 'None', value: '/' },
          color: this.defaultColor,
          position: [i, j],
          musicfiles: [],
        };
      }
    }

    return buttonData;
  }

  updateRowsColumns(size: number[]) {
    this.defaultColor = environment.defaultButtonColor;

    let diffX = size[0] - this.gridSizeX;
    let diffY = size[1] - this.gridSizeY;
    let oldGridSizeX = this.gridSizeX;

    this.gridSizeX = size[0];
    this.gridSizeY = size[1];

    if (diffX != 0) {
      if (diffX > 0) {
        console.log('X erhhung');
        for (let h = 0; h < this.displayTypes; h++) {
          for (let i = oldGridSizeX; i < this.gridSizeX; i++) {
            this.matrixData[h][i] = [];
            for (let j = 0; j < this.gridSizeY; j++) {
              this.matrixData[h][i][j] = {
                name: 'Button' + (i + j * this.gridSizeY),
                action: { name: 'None', value: '/' },
                color: this.defaultColor,
                position: [i, j],
                musicfiles: [],
              };
            }
          }
        }
      } else {
        for (let h = 0; h < this.displayTypes; h++) {
          for (let i = oldGridSizeX; i > this.gridSizeX; i--) {
            this.matrixData[h].pop();
          }
        }
      }
    } else if (diffY != 0) {
      if (diffY > 0) {
        for (let h = 0; h < this.displayTypes; h++) {
          for (let i = 0; i < this.gridSizeX; i++) {
            for (let j = 0; j < diffY; j++) {
              this.matrixData[h][i].push({
                name: 'Button' + (i + j * this.gridSizeY),
                action: { name: 'None', value: '/' },
                color: this.defaultColor,
                position: [i, j],
                musicfiles: [],
              });
            }
          }
        }
      } else {
        for (let h = 0; h < this.displayTypes; h++) {
          for (let i = 0; i < this.gridSizeX; i++) {
            for (let j = 0; j < diffY; j++) {
              this.matrixData[h][i].pop();
            }
          }
        }
      }
    }

    this.matrixDataService.changeData(this.matrixData);

    this.rows = [];
    this.columns = [];
    for (let i = 0; i < this.gridSizeY; i++) {
      this.rows.push(i);
    }
    for (let i = 0; i < this.gridSizeX; i++) {
      this.columns.push(i);
    }
  }

  zipAndDownloadConfig() {
    this.generateZipFS(this.matrixData);
  }

  generateZipFS(mData: ButtonData[][][]) {
    this.loadingData.enabled = true;
    this.loadingData.text = 'Starting Compression...';
    let zip = new JSZip();
    let root = zip.folder('musicPad');
    root.file('bmap.txt', this.generateBMap(mData));

    for (let h = 0; h < this.displayTypes; h++) {
      for (let i = 0; i < this.gridSizeX; i++) {
        for (let j = 0; j < this.gridSizeY; j++) {
          if (
            mData[h][i][j].action.value == 'MF' ||
            mData[h][i][j].action.value == 'SE'
          ) {
            if (h == 0) {
              let folderName: string = i + 'X' + j;
              let mf = root.folder(folderName);
              this.loadingData.text = 'Generating folder structure';
              for (let k = 0; k < mData[h][i][j].musicfiles.length; k++) {
                mf.file(k + '.wav', mData[h][i][j].musicfiles[k]);
              }
            } else if (h == 1) {
              let folderName: string = i + 'D' + j;
              let mf = root.folder(folderName);
              this.loadingData.text = 'Generating folder structure';
              for (let k = 0; k < mData[h][i][j].musicfiles.length; k++) {
                mf.file(k + '.wav', mData[h][i][j].musicfiles[k]);
              }
            }
          }
        }
      }
    }

    let that = this;
    zip
      .generateAsync({ type: 'blob' }, function updateCallback(metadata) {
        that.loadingData.text = 'Compressing File: ' + metadata.currentFile;
        that.loadingData.progress = Number(metadata.percent.toFixed(2));
      })
      .then(
        function (blob) {
          that.loadingData.text = 'Saving Data';
          FileSaver.saveAs(blob, 'data.zip');
          that.loadingData.enabled = false;
        },
        function (err) {
          console.log(err);
        }
      );
  }

  generateBMap(arrayMap: ButtonData[][][]): string {
    let bmapString: string =
      '/*Dont Edit this File if you dont know what you are doing! Buttonmap*/\n';
    bmapString += 'SizeX:' + this.gridSizeX + '\n';
    bmapString += 'SizeY:' + this.gridSizeY + '\n';
    bmapString += 'dc:' + this.hexToInt(this.defaultColor.hex) + '\n';
    bmapString += 'db:' + this.defaultBrightness + '\n';
    bmapString += 'da:' + this.defaultAnimation + '\n';
    console.log(arrayMap);

    for (let h = 0; h < this.displayTypes; h++) {
      for (let i = 0; i < this.gridSizeX; i++) {
        for (let j = 0; j < this.gridSizeY; j++) {
          if (
            arrayMap[h][i][j].action.value != '/' ||
            arrayMap[h][i][j].color.hex != this.defaultColor.hex
          ) {
            let type = (arrayMap[h][i][j].action.type != undefined) ? ('|' + arrayMap[h][i][j].action.type) : ('');
            if (h == 0) {
              let line: string = i + 'X' + j + ':';
              line +=
                arrayMap[h][i][j].action.value + type +
                ',' +
                this.hexToInt(arrayMap[h][i][j].color.hex) +
                '\n';
              bmapString += line;
            } else if (h == 1) {
              let line: string = i + 'D' + j + ':';
              line += arrayMap[h][i][j].action.value + type + '\n';
              bmapString += line;
            }
          }
        }
      }
    }
    return bmapString;
  }

  hexToInt(hex: string): number {
    const parsed = parseInt(hex, 16);
    if (isNaN(parsed)) {
      return 0;
    }
    return parsed;
  }

  createBlobFromText(data: string): Blob {
    return new Blob([data], {
      type: 'text/plain;charset=utf-8',
    });
  }

  /**
   * Returns a deep copy of the object
   */
  deepCopy(oldObj: any) {
    var newObj = oldObj;
    if (oldObj && typeof oldObj === 'object') {
      if (oldObj instanceof Date) {
        return new Date(oldObj.getTime());
      }
      newObj =
        Object.prototype.toString.call(oldObj) === '[object Array]' ? [] : {};
      for (var i in oldObj) {
        newObj[i] = this.deepCopy(oldObj[i]);
      }
    }
    return newObj;
  }

  actionToggleChange() {
    if (this.actionToggleStatus) {
      this.displayedActionType = 0;
    } else {
      this.displayedActionType = 1;
    }
  }
}
