import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { env } from 'process';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-settings-sidenav',
  templateUrl: './settings-sidenav.component.html',
  styleUrls: ['./settings-sidenav.component.scss']
})
export class SettingsSidenavComponent implements OnInit {
  opend: boolean = false;

  @Output('size') matrixSizeEmiter: EventEmitter<any> = new EventEmitter();
  @Input('sizeX') matrixSizeX;
  @Input('sizeY') matrixSizeY;

  @Output('brightness') brightnessEmiter: EventEmitter<any> = new EventEmitter();
  brightness = environment.defaultBrightness;

  @Output('defAni') defaultAnimationEmitter: EventEmitter<any> = new EventEmitter();
  defaultAnimation = 'sbc';
  animations = environment.bgAniOptions;

  @Output('clear') clear: EventEmitter<any> = new EventEmitter();
  @Output('loadDefault') loadDefault: EventEmitter<any> = new EventEmitter();
  @Output('downloadSetting') downloadSetting: EventEmitter<any> = new EventEmitter();
  @Output('loadSetting') loadSetting: EventEmitter<any> = new EventEmitter();

  @Output('generateBmap') generateBmap: EventEmitter<any> = new EventEmitter();
  @Output('generateConfig') generateConfig: EventEmitter<any> = new EventEmitter();

  @ViewChild('file')
  fileInput: ElementRef;

  constructor() { }

  ngOnInit(): void {
  }

  processConfigFile(files) {
    let that = this;
    let fs = new FileReader();
    fs.onload = function () {
      that.loadSetting.emit(fs.result);
      that.fileInput.nativeElement.value = "";
    }

    fs.readAsText(files[0]);
  }

  emitBrightness() {
    this.brightnessEmiter.emit(this.brightness);
  }

  emitMatrixSize() {
    this.matrixSizeEmiter.emit([this.matrixSizeX, this.matrixSizeY]);
  }

}
