import { ButtonData } from './../sites/main/main.component';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class MatrixDataService {
  private dataSource = new BehaviorSubject<ButtonData[][][]>([[]]);
  currentData = this.dataSource.asObservable();

  private activeStateData = new BehaviorSubject<ButtonData[][][]>([[]]);
  currentActive = this.activeStateData.asObservable();
  constructor() { }

  changeData(data: ButtonData[][][]) {
    this.dataSource.next(data);
  }

  aciveState(state: ButtonData[][][]) {
    this.activeStateData.next(state);
  }
}
