import { MatrixDataService } from './../../services/matrix-data.service';
import { ButtonData } from './../../sites/main/main.component';
import { Component, ElementRef, OnInit, ViewChild, Input } from '@angular/core';
import { MatSidenav } from '@angular/material/sidenav';
import { environment } from 'src/environments/environment';
import { ThemePalette } from '@angular/material/core';
import { AbstractControl, FormControl, Validators } from '@angular/forms';
import { Action } from '../../sites/main/main.component';
import { Serializer } from 'v8';

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.scss'],
})
export class SidenavComponent implements OnInit {
  opend: boolean = false;

  matrixData: ButtonData[][][] = [[]];

  selectedButton: number[] = [0, 0];

  actions: Action[] = environment.actions;
  bgAnimations = environment.bgAniOptions;

  public disabled = false;
  public color: ThemePalette = 'primary';
  public touchUi = false;

  colorCtr: AbstractControl = new FormControl(null, [Validators.required]);

  @ViewChild('drawer') sidenav: MatSidenav;

  @Input('displayedActionType') displayedActionType;

  constructor(private matrixDataService: MatrixDataService) { }

  ngOnInit(): void {
    this.matrixDataService.currentData.subscribe((res) => {
      this.matrixData = res;
      console.log(res);
    });
  }

  openButtonConfig(x, y) {
    this.selectedButton = [x, y];
    this.opend = true;
    this.sidenav.open();
    console.log(x, y);
  }

  checkForbiddenChar(event) {
    let k = String.fromCharCode(event.charCode);
    if (environment.forbiddenChars.includes(k)) {
      return false;
    }
    return true;
  }

  updateMatrix() {
    console.log(
      this.matrixData[this.displayedActionType][this.selectedButton[0]][
        this.selectedButton[1]
      ].action.type
    )
    this.matrixDataService.changeData(this.matrixData);
  }
}
